<template>
	<div class="card-item" @click="navigate">
		<va-icon :name="icon" color="c3" class="ml-2"></va-icon>
		<div class="card-item-number" :style="getColor(color)">{{number}}</div>
		<div class="card-item-subtile">{{subtitle}} </div>
	</div>
</template>

<script>
export default {
	name: 'DashboardCardItems',
	props : ['subtitle','icon', 'number', 'color', 'aba', 'generalState'],
	methods: {
		getColor(color){
			return 'color:'+color+';'
		},
		navigate(){
            this.$router.push({path: '/sistema/servicos', params: {loadAll: false}})
			this.$store.dispatch('abas.set', ['/sistema/servicos', this.aba.index])
			setTimeout(()=>{
				this.$store.dispatch('abas.setFiltro', [this.aba.tipo, {
					page: 1,
					generalState: this.generalState
				}])
			},500)
		},
	},
}
</script>

<style scoped>

.card-item {
	height: 35px;
	background-color: var(--cinza);
	padding: 5px;
	margin: 10px;
	border-radius: 10px;
	display: flex;
	flex-direction: row;
	align-items: center;
}
.card-item:hover{
	cursor: pointer;
}
.card-item-number{
	padding: 0px 20px;
	font-size: 1.6em;
	font-weight: bold;
}
.card-item-subtile{
	/* padding-left: 10px; */
	font-size: 1.1em;
	color: var(--c3);
}

</style>