<template>
<div class="system-topbar-filter">
	<va-select clearable v-model="filtroData" label="Intervalo" :options="['Mês atual', 'Último dia', 'Última semana', 'Último mês', 'Personalizado']" :color="color"></va-select>
	<va-date-input label="Filtro de data" v-model="dtStartEnd" v-if="filtroData == 'Personalizado'" :color="color"/>
</div>
</template>

<script>
export default {
    name: 'SystemTopbarFiltro',
	props: ["color", ],
	data() {
		return {
			filtroData: ""
		}
	},
    computed: {
        getAbas(){
            return this.$store.getters['abas.getAbas'][this.$route.path]
        },
        aba() {
            const index = this.$store.getters["abas.getIndices"][this.$route.path];
            const abas = this.$store.getters["abas.getAbas"][this.$route.path];
            return index != undefined && abas != undefined ? abas[index] : {};
        },
		getFiltro(){
			const filtros = this.$store.getters['abas.getFiltro']
			return filtros && filtros[this.aba.tipo] ? filtros[this.aba.tipo] : {}
		},
        dtStartEnd: {
			get(){
				return {
					'start': this.getFiltro['dtStart'] ? this.getFiltro['dtStart'] : null,
					'end':   this.getFiltro['dtEnd'] ? this.getFiltro['dtEnd'] : null
				}
			},
			set(value){
				this.$store.dispatch('abas.setFiltro', [this.aba.tipo, {dtStart: value.start, dtEnd: value.end}])
			}
        }
    },
	watch: {
		filtroData(value){
			const date = new Date();
			let dtStart = date
			let dtEnd = new Date(date.getFullYear(), date.getMonth(), 50)
			switch (value) {
				case 'Último dia':
					dtStart -= 86400*1000
					break;
				case 'Última semana':
					dtStart -= 86400*1000*7
					break;
				case 'Último mês':
					dtStart -= 86400*1000*30
					break;
				case 'Mês atual':
					dtStart = new Date(date.getFullYear(), date.getMonth(), 1)
					break;
				default:
					dtStart = null
					dtEnd   = null
					break;
			}
			if (dtStart) dtStart = new Date(dtStart)
			if (dtEnd)   dtEnd   = new Date(dtEnd)
			this.dtStartEnd = {
				start: dtStart, 
				end: dtEnd
			}
		},
		dtStartEnd(){
			this.$store.dispatch('dashboard.loadResumo', 'get_service_resume')
			this.$store.dispatch('dashboard.loadResumo', 'get_calleds_resume')
			this.$store.dispatch('dashboard.loadResumo', 'get_reports_resume')
			this.$store.dispatch('dashboard.loadResumo', 'get_inspections_resume')
		}
	},
	mounted(){
		//this.filtroData = 'Mês atual' 
	}

}
</script>

<style scoped>
.system-topbar-filter{
	display: flex;
    justify-content: center;
    align-items: center;
}
.system-topbar-filter * {
	margin: 0 5px;
}
</style>